import React, {
  createContext,
  useReducer,
  useState,
  useEffect,
  useContext,
} from 'react';
import moment from 'moment';
import PropTypes from 'prop-types';
import dbdbdb from 'dbdbdb';

export const Store = createContext();
const reducer = (oldState, newState) => ({ ...oldState, ...newState });

export const Dropbox = createContext();
const db = dbdbdb({ clientId: 'uaqu46389soi02n' });

function DropboxProvider({ children }) {
  return <Dropbox.Provider value={db}>{children}</Dropbox.Provider>;
}

function StoreProvider({ children, currentDay }) {
  const [database, setDatabase] = useState({});
  const { readDatabase } = useContext(Dropbox);
  const [databaseFetched, setDatabaseFetched] = useState(false);
  const [state, setState] = useReducer(reducer, {
    currentDay: currentDay || moment(),
  });

  useEffect(() => {
    if (!databaseFetched) {
      readDatabase().then(db => {
        setDatabase(db);
        setDatabaseFetched(true);
      });
    }
  });

  const store = { state, setState, database };
  return <Store.Provider value={store}>{children}</Store.Provider>;
}

export default function AppProviders({ children, ...restProps }) {
  return (
    <DropboxProvider>
      <StoreProvider {...restProps}>{children}</StoreProvider>
    </DropboxProvider>
  );
}

AppProviders.propTypes = {
  children: PropTypes.node.isRequired,
};
