import { useContext, useEffect, useState } from 'react';
import { Dropbox } from 'app/Providers';

export function useDb() {
  const db = useContext(Dropbox);
  const { getClient, createClient, logOutDropbox } = db;
  const [user, setUser] = useState(null);
  const [client, setClient] = useState(getClient() || createClient());

  useEffect(
    () => {
      client && client.usersGetCurrentAccount().then(setUser);
      window.history.pushState(
        {},
        document.title,
        process.env.PUBLIC_URL || '/'
      );
    },
    [client]
  );

  function logout() {
    setUser(null);
    setClient(null);
    logOutDropbox();
  }

  return {
    ...db,
    client,
    logout,
    user,
  };
}
