import React, { Suspense } from 'react';
import { getTodos, getTodoHistory, useDb } from 'shared';
import AppProviders from 'app/Providers';
import { unstable_createResource as createResource } from 'react-cache';
import TodoList from 'app/TodoList';

const todosResource = createResource(getTodos);
const todosHistoryResource = createResource(getTodoHistory);

const Fallback = () => <div>Loading...</div>;

const LiveTodoList = () => {
  const todos = todosResource.read();
  const history = todosHistoryResource.read();
  return <TodoList defaultTodos={todos} defaultHistory={history} />;
};

const User = () => {
  const { authUrl, client, logout, user } = useDb();

  if (user) {
    const { name } = user;
    return (
      <div>
        Logged in as: {name.display_name}&nbsp;
        <button onClick={logout}>Logout</button>
      </div>
    );
  }

  return client ? (
    'Fetching account...'
  ) : (
    <a href={authUrl}>Login to Dropbox</a>
  );
};

export default function App(props) {
  return (
    <AppProviders>
      <User />
      <Suspense fallback={<Fallback />}>
        <LiveTodoList />
      </Suspense>
    </AppProviders>
  );
}
